@import "../../styles/variables";

.weather-container {
  flex: 1;
  height: 100vh;
  background-color: $project-color-main;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;

  &:before {
    position: absolute;
    content: "";
    width: 600px;
    height: 600px;
    border-radius: 50%;
    background-color: $project-color-main-circle;
    opacity: 0.2;
    box-shadow: 0 0 10px 1px black;
  }
}

.img {
  width: 50px;
  height: 50px;
}

.city {
  font-size: 34px;
}

.temp {
  font-size: 145px;
}

.description {
  font-size: 45px;
}

.sub-description {
  text-transform: capitalize;
}

.city,
.temp,
.description {
  margin: 10px;
}

@media (max-width: 980px) {
  .weather-container {
    &:before {
      width: 500px;
      height: 500px;
    }
  }
}

@media (max-width: 790px) {
  .weather-container {
    &:before {
      width: 380px;
      height: 380px;
    }
  }

  .city {
    font-size: 24px;
  }

  .temp {
    font-size: 105px;
  }

  .description {
    font-size: 35px;
  }
}

@media (max-width: 680px) {
  .weather-container {
    width: 100%;
    min-height: 500px;
  }
}
