@import "../../styles/variables";

.sidebar-container {
  width: 300px;
  height: 100vh;
  background-color: $project-color-sidebar;
}

@media (max-width: 980px) {
  .sidebar-container {
    width: 240px;
  }
}

@media (max-width: 680px) {
  .sidebar-container {
    width: 100%;
    max-height: 300px;
    overflow-y: auto;
  }
}
