@import "../../../../styles/variables";

.add-city-container {
  display: flex;
}

.btn-add-city {
  border: 1px solid gray;
  opacity: 0.5;
  background-color: $project-color-btn;
  font-family: inherit;
  cursor: pointer;

  &:hover {
    opacity: 1;
  }
}

.add-city-input {
  background-color: white;
  border: 1px solid gray;
  padding-left: 10px;

  &-error {
    border: 1px solid tomato;
  }

  &:focus {
    border: 1px solid lightseagreen;
  }
}

.btn-add-city,
.add-city-input {
  outline: none;
  border-radius: $project-item-radius;
  width: 100%;
  height: 36px;
}
